.Auth {
    display: flex;
    justify-content: center;
    padding-top: 100px;
    flex-grow: 1;
    width: 100%;
    background: linear-gradient(270deg, #cef2fa 0%, #59bfef 100%);
}

.Auth > div {
    width: 100%;
    max-width: 400px;
    padding: 0 20px;
}

.Auth h1 {
    color: #fff;
    text-align: center;
}

.AuthForm {
    background: #fff;
    /*box-shadow: 2px 2px 2px rgba(0, 0, 0, .5);*/
    padding: 15px;
    border-radius: 10px;
}

.AuthForm > span {
    color: #f01f30;
}